twitterwidget:last-child,
twitter-widget:last-child {
  margin: 0 !important;
}

twitterwidget + p:last-child > script,
twitter-widget + p:last-child > script {
  display: block;
  margin-top: calc(var(--space) * -1);
}

.mejs-container:not(:last-child) {
  margin-bottom: var(--space);
}

.mejs-container a {
  border-bottom: none !important;
  text-decoration: none !important;
}

.wp-block-image {
  margin-bottom: var(--space);
}

.wp-block-image .alignleft:after,
.wp-block-image .alignright:after {
  content: '';
  display: block;
  height: var(--space);
}

.wp-block-button {
  margin-bottom: var(--space);
}

.wp-block-cover {
  margin-bottom: var(--space);
}
